@media (max-width: 1285.87px) {
    #root {
        padding: 0 8px;
    }

    .trusted-brands-inner {
        padding-left: 13px;
        padding-right: 13px;
    }

    .footer-info_companyinfo {
        max-width: fit-content;
    }

    .footer-info {
        max-width: 683px;
    }
    
}



@media (max-width: 1063px) {

    .nts-nd-button {
        display: none;
    }
    .nts-nd-button.media {
        display: inline-block;
        margin-top: 45px;
    }

    .new-tools-section {
        justify-content: center;
    }

    .swiper-section {
        margin-top: 121px;
    }

    .nts-newcommer-div {
        text-align: center;
    }

    .nts-newcommer-apps {
        max-width: 950px;
    }

    .mp-app.modif {
        max-width: initial;
    }


    
}