.mostpopular-section {
    
    margin: 200px auto 0;
}

.mp_heading {
    font-weight: 500;
    font-size: em(48);
    line-height: 130%;
    /* or 62px */

    text-align: center;

    color: rgba(255, 255, 255, 0.9);
}

.mp_description {
    font-weight: 500;
    font-size: em(18);
    line-height: 30px;
    /* or 167% */
    text-align: center;
    
    max-width: 427px;
    margin: 25px auto 0;

    color: rgba(255, 255, 255, 0.55);

}

.mp-apps-cont {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    
    max-width: 1440px;
    margin: 110px auto 0;
}

.mp-app {
    flex-basis: 392px;
    
    min-height: 294px;
    
    background: #1E252B;
    border-radius: 15px;
    padding: 33px ;

}

.mp-app-icon.figma {
    width: 64px;
    height: 64px;
    margin-bottom: 31px;
    
}

.mp-app-icon.sketch {
    width: 64px;
    margin-bottom: 34.05px;
}

.mp-app-icon.vscode {
    width: 65px;
    margin-right: 29.76px;
    margin-bottom: 31.16px;
}

.mp-app-icon.notion {
    width: 64px;
    margin-bottom: 31px;
}

.mp-app-icon.slack {
    width: 65px;
    margin-bottom: 31.18px;
}

.mp-app-icon.invis {
    width: 65px;
    margin-right: 29.76px;
    margin-bottom: 31.18px;
}

.mp-app-icon {
    margin-right: 30px;
    float: left;
}

.mp-app_clear {
    clear: both;
}

.mp-app_name {
    font-weight: 500;
    font-size: em(24);
    line-height: 1.5;
    /* identical to box height */


    color: rgba(255, 255, 255, 0.78);

}

.mp-app_description {
    font-style: normal;
    font-weight: normal;
    font-size: em(18);
    line-height: 1.5;
    margin-top: 3px;
    /* identical to box height */


    color: rgba(255, 165, 55, 0.55);
}

.mp-app_navbar {
    display: flex;
    margin-top: 30px;

}

.mp-app_icon-visit {
    background: #1E252B;
    border-radius: 8px;
    color: white;
    width: 106px;
    height: 42px;
    font-weight: 600;
    font-size: em(16);
    line-height: 1.5;
    /* identical to box height */


    color: rgba(255, 255, 255, 0.55);
}

.mp-app_icon-cont {
    width: 42px;
    height: 42px;
}

.mp-app_text {  
    font-style: normal;
    font-weight: normal;
    font-size: em(16);
    line-height: 1.875;
    /* or 187% */
    


    color: rgba(255, 255, 255, 0.55);

}

.mp-app_icon-like {
    display: block;
    margin: 7px auto 0;
    width: 28px;
    height: 28px;
}

.mp-app_icon-add {
    display: block;
    margin: 7px auto 0px;
    width: 30px;
    height: 30px;
}

.load-more-button {
    background: #1E252B;
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-sizing: border-box;
    border-radius: 8px;
    width: 184px;
    height: 64px;
    color: white;
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: em(20);
    line-height: 1.5;
    /* identical to box height */
    display: block;
    margin: 100px auto 0;
    
   
    color: #FF6E30;
    transition: 0.35s ease-in-out;
}