.header {
    max-width: 1224px;
    width: 100%;
    height: 48px;
    display: flex;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
    margin-top: 26px;

}

.header-logo {
    width: 38px;
    height: 38px;
}

.header-logo-text {
    display: flex;
    font-size: em(22);
    line-height: 120%;
    align-items: center;
    max-height: 100%;
    width: 16.584%;
    min-width: fit-content;
}

.header-text {
    margin-left: 12px;
}

.header-navbar {
    max-width: 431px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

.header-navlink {
    font-size: 1em;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.55);
    
}


.header-sign-cont {
    display: flex;
    align-items: center;
    height: 100%;
    
    
}

.heaer-login {
    color: rgba(255, 255, 255, 0.55);
    font-weight: 600;
    font-size: 1em;
}

.header-signup {
    background: #FF6E30;
    width: 118px;
    height: 100%;
    border-radius: 8px;
    
    font-size: em(16);
    line-height: 120%;
    color: #FDF5FF;
    font-weight: 600;
    margin-left: 41px;
    padding: 0;
}